import React from "react";
import { Carousel } from "react-responsive-carousel";

import "react-responsive-carousel/lib/styles/carousel.min.css";

const ImgCarousel = ({ links, lang }) => {
  return (
    <Carousel
      showThumbs={false}
      swipeable={true}
      infiniteLoop={true}
      useKeyboardArrows={true}
      dynamicHeight={true}
      emulateTouch={true}
      statusFormatter={(current, total) =>
        `${lang === "sk" ? "Obrázok" : "Image"} ${current} ${
          lang === "sk" ? "z" : "of"
        } ${total}`
      }
    >
      {links.length > 0 &&
        links.map((link) => (
          <div key={link}>
            <img alt="" src={link} />
          </div>
        ))}
    </Carousel>
  );
};

export default ImgCarousel;
