import React from "react";

const Spotlight = ({ lang }) => {
  const texts = {
    sk: {
      SPOTLIGHT_CATCHPHRASE: "Rozprávkové šperky",
      CTA: "Pozrieť produkty",
    },
    en: {
      SPOTLIGHT_CATCHPHRASE: "Dreamy jewelry",
      CTA: "Check out products",
    },
  };

  const scrollToProducts = (e) => {
    e.preventDefault();
    document.getElementById("products").scrollIntoView({ behavior: "smooth" });
  };

  return (
    <div className="spotlight">
      <h2>{texts[lang]["SPOTLIGHT_CATCHPHRASE"]}</h2>
      <a href="#products" id="cta" onClick={(e) => scrollToProducts(e)}>
        {texts[lang]["CTA"]}
      </a>
    </div>
  );
};

export default Spotlight;
