import React, { useState } from "react";
import { projectStorage, projectFirestore } from "../firebase/config";

const AddItemForm = ({ lang }) => {
  const initialState = {
    name: "",
    price: "",
    image: null,
  };

  const [formState, setFormState] = useState(initialState);
  const [error, setError] = useState(null);

  const texts = {
    sk: {
      FORM_NAME: "Názov",
      FORM_PRICE: "Cena",
      FORM_IMAGES: "Obrázky",
      FORM_SUBMIT: "Pridať",
      FORM_REQUIRED: "Položka je povinná",
      FORM_TITLE: "Pridať novú položku",
    },
    en: {
      FORM_NAME: "Name",
      FORM_PRICE: "Price",
      FORM_IMAGES: "Images",
      FORM_SUBMIT: "Create",
      FORM_REQUIRED: "Field is required",
      FORM_TITLE: "Add new item",
    },
  };

  async function addNewItem() {
    const urls = [];
    try {
      const storageRef = projectStorage.ref();
      for (const item of formState.image) {
        const uploadTask = await storageRef.child(`images/${item.name}`);
        const URL = await uploadTask.put(item).snapshot.ref.getDownloadURL();
        urls.push(URL);
      }
      await projectFirestore.collection("items").add({
        name: formState.name,
        price: formState.price,
        available: true,
        image: urls,
      });
    } catch (error) {
      setError({ message: error.message });
      setTimeout(() => {
        setError(null);
      }, 10000);
    }
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    addNewItem();
  };

  const handleChange = (e) => {
    if (e.target.name === "image") {
      setFormState({ ...formState, [e.target.name]: e.target.files });
    } else {
      setFormState({ ...formState, [e.target.name]: e.target.value });
    }
  };

  return (
    <div className="container">
      <h2>{texts[lang]["FORM_TITLE"]}</h2>
      {error && error.message}
      <form onSubmit={handleSubmit}>
        <label className="required" htmlFor="name">
          {texts[lang]["FORM_NAME"]}
        </label>
        <input
          type="text"
          name="name"
          required
          value={formState.name}
          onChange={handleChange}
        />
        <label className="required" htmlFor="name">
          {texts[lang]["FORM_PRICE"]}
        </label>
        <input
          type="text"
          inputMode="numeric"
          pattern="[0-9]*"
          name="price"
          required
          value={formState.price}
          onChange={handleChange}
        />
        <label className="required" htmlFor="name">
          {texts[lang]["FORM_IMAGES"]}
        </label>
        <input
          type="file"
          required
          name="image"
          multiple
          onChange={handleChange}
        />
        <button type="submit">{texts[lang]["FORM_SUBMIT"]}</button>
        <p className="required hint"> {texts[lang]["FORM_REQUIRED"]}</p>
      </form>
    </div>
  );
};

export default AddItemForm;
