import React, { useState } from "react";
import useFetch from "../hooks/useFetch";
import { Link } from "react-router-dom";
import { motion } from "framer-motion";

const ImgGallery = ({ lang }) => {
  const texts = {
    sk: {
      FETCHING_DATA: "Načítavam dáta..",
      NO_PRODUCTS: "Žiadne produkty",
      PRODUCTS: "Produkty",
      ITEM_AVAILABLE: "Dostupné",
      ITEM_UNAVAILABLE: "Nedostupné",
      LOAD_MORE: "Načítať ďalšie..",
    },
    en: {
      FETCHING_DATA: "Fetching data..",
      NO_PRODUCTS: "No products available",
      PRODUCTS: "Products",
      ITEM_AVAILABLE: "Available",
      ITEM_UNAVAILABLE: "Unavailable",
      LOAD_MORE: "Load more..",
    },
  };
  const itemsToFetch = 6;
  const [limit, setLimit] = useState(itemsToFetch);

  const { docs, size } = useFetch("items");

  if (!docs) {
    return (
      <div className="container">
        <p id="products">{texts[lang]["FETCHING_DATA"]}</p>
      </div>
    );
  }

  if (docs.length < 1) {
    return (
      <div className="container">
        <p id="products">{texts[lang]["NO_PRODUCTS"]}</p>
      </div>
    );
  }

  const increaseLimit = () => {
    setLimit((limit) => limit + itemsToFetch);
  };

  return (
    <div className="container" id="products">
      <h2>{texts[lang]["PRODUCTS"]}</h2>
      <motion.div className="gallery" layout>
        {docs.map((item, index) => {
          if (index < limit) {
            return (
              <Link key={item.id} to={`/item/${item.id}`}>
                <article className="gallery__item">
                  <h2>{item.name}</h2>
                  <img src={`${item.image[0]}`} alt="" />
                  <p>
                    {item.price}€{" "}
                    {item.available ? (
                      <span style={{ color: "lightgreen" }}>
                        {texts[lang]["ITEM_AVAILABLE"]}
                      </span>
                    ) : (
                      <span style={{ color: "red" }}>
                        {texts[lang]["ITEM_UNAVAILABLE"]}
                      </span>
                    )}
                  </p>
                  <button>Detail</button>
                </article>
              </Link>
            );
          }
          return "";
        })}
      </motion.div>
      {size > limit && (
        <button onClick={() => increaseLimit()}>
          {texts[lang]["LOAD_MORE"]}
        </button>
      )}
    </div>
  );
};

export default ImgGallery;
