import React, { useState, useEffect } from "react";
import { projectFirestore } from "../firebase/config";
import { useParams } from "react-router-dom";
import ImgCarousel from "./ImgCarousel";

const SingleItem = ({ lang }) => {
  const { id } = useParams();
  const [item, setItem] = useState(null);

  useEffect(() => {
    async function fetchData() {
      const fetchedItem = await projectFirestore
        .collection("items")
        .doc(id)
        .get();
      if (fetchedItem.exists) {
        setItem(fetchedItem.data());
      }
    }
    fetchData();
  }, [id]);

  if (!item) {
    return (
      <div className="container">
        <p>Nacitavam data</p>
      </div>
    );
  }

  const texts = {
    sk: {
      ITEM_TYPE: "Náušnice",
      ITEM_AVAILABLE: "Dostupné",
      ITEM_UNAVAILABLE: "Nedostupné",
    },
    en: {
      ITEM_TYPE: "Earrings",
      ITEM_AVAILABLE: "Available",
      ITEM_UNAVAILABLE: "Unavailable",
    },
  };

  return (
    <div className="container single-item">
      <h2>
        {texts[lang]["ITEM_TYPE"]} {item.name}
      </h2>
      <ImgCarousel links={item.image} lang={lang} />
      <p>
        {item.available
          ? texts[lang]["ITEM_AVAILABLE"]
          : texts[lang]["ITEM_UNAVAILABLE"]}
      </p>
      <p>{item.price}€</p>
    </div>
  );
};

export default SingleItem;
