import React, { useCallback, useContext } from "react";
import { withRouter, Redirect } from "react-router-dom";
import { projectAuth } from "../firebase/config";
import { AuthContext } from "../firebase/Auth";

const Login = ({ history, lang }) => {
  const handleLogin = useCallback(
    async (event) => {
      event.preventDefault();
      const { email, password } = event.target.elements;
      try {
        await projectAuth.signInWithEmailAndPassword(
          email.value,
          password.value
        );
        history.push("/admin");
      } catch (err) {
        alert(err);
      }
    },
    [history]
  );

  const texts = {
    sk: {
      FORM_LOGIN: "Email",
      FORM_PASSWORD: "Heslo",
      FORM_SUBMIT: "Prihlásiť",
    },
    en: {
      FORM_LOGIN: "Email",
      FORM_PASSWORD: "Password",
      FORM_SUBMIT: "Log In",
    },
  };

  const { currentUser } = useContext(AuthContext);

  if (currentUser) {
    return <Redirect to="/admin" />;
  }

  return (
    <div className="container">
      <h2>Log In</h2>
      <form onSubmit={handleLogin}>
        <label htmlFor="email">{texts[lang]["FORM_LOGIN"]}</label>
        <input type="email" name="email" />
        <label htmlFor="password">{texts[lang]["FORM_PASSWORD"]}</label>
        <input type="password" name="password" />
        <button type="submit">{texts[lang]["FORM_SUBMIT"]}</button>
      </form>
    </div>
  );
};

export default withRouter(Login);
