import React from "react";
import { Link } from "react-router-dom";

const Header = ({ lang, setLang }) => {
  return (
    <header>
      <h1>
        <Link to="/">Oringle</Link>
      </h1>
      {lang === "sk" && <button onClick={() => setLang("en")}>EN</button>}
      {lang === "en" && <button onClick={() => setLang("sk")}>SK</button>}
    </header>
  );
};

export default Header;
