import React, { Fragment } from "react";
import { Link } from "react-router-dom";

const Footer = ({ lang }) => {
  const texts = {
    sk: {
      FOOTER_TEXT: (
        <Fragment>
          Túto stránku vytvoril{" "}
          <a href="https://dominikjuriga.me">Dominik Juriga</a>. &copy; Oringle
          2020.
        </Fragment>
      ),
    },
    en: {
      FOOTER_TEXT: (
        <Fragment>
          This website has been created by{" "}
          <a href="https://dominikjuriga.me">Dominik Juriga</a>. &copy; Oringle
          2020.
        </Fragment>
      ),
    },
  };
  return (
    <footer>
      <p>{texts[lang]["FOOTER_TEXT"]}</p> <Link to="/admin">Management.</Link>
    </footer>
  );
};

export default Footer;
