import React, { useEffect, useState } from "react";
import { projectFirestore } from "../firebase/config";
const AdminItemList = ({ lang }) => {
  const [items, setItems] = useState([]);
  async function fetchItems() {
    const res = await projectFirestore.collection("items").get();
    if (!res.empty) {
      let docs = [];
      res.forEach((doc) => {
        docs.push({ ...doc.data(), id: doc.id });
      });
      setItems(docs);
    }
  }
  useEffect(() => {
    fetchItems();
  }, []);

  const deleteItem = async (id) => {
    await projectFirestore.collection("items").doc(id).delete();
    fetchItems();
  };

  const changeAvailability = async (id, available) => {
    const item = await projectFirestore.collection("items").doc(id);
    await item.update({
      available: !available,
    });
    fetchItems();
  };

  const texts = {
    sk: {
      ITEM_TITLE: "Zoznam položiek",
      ITEM_AVAILABLE: "Dostupné",
      ITEM_UNAVAILABLE: "Nedostupné",
      BUTTON_DELETE: "Zmazať",
      BUTTON_AVAILABLE: "Dostupné",
      BUTTON_UNAVAILABLE: "Nedostupné",
      TABLE_TITLE: "Názov",
      TABLE_PRICE: "Cena",
      TABLE_AVAILABILITY: "Dostupnosť",
      TABLE_DELETE: "Zmaž",
      TABLE_CHANGE: "Zmeň stav",
    },
    en: {
      ITEM_TITLE: "Item List",
      ITEM_AVAILABLE: "Available",
      ITEM_UNAVAILABLE: "Unavailable",
      BUTTON_DELETE: "Remove",
      BUTTON_AVAILABLE: "Available",
      BUTTON_UNAVAILABLE: "Unavailable",
      TABLE_TITLE: "Name",
      TABLE_PRICE: "Price",
      TABLE_AVAILABILITY: "Availability",
      TABLE_DELETE: "Remove",
      TABLE_CHANGE: "Change Availability",
    },
  };

  return (
    <div className="container">
      <h2>{texts[lang]["ITEM_TITLE"]}</h2>
      {items.length > 0 && (
        <table>
          <thead>
            <tr>
              <th>{texts[lang]["TABLE_TITLE"]}</th>
              <th>{texts[lang]["TABLE_PRICE"]}</th>
              <th>{texts[lang]["TABLE_AVAILABILITY"]}</th>
              <th>{texts[lang]["TABLE_DELETE"]}</th>
              <th>{texts[lang]["TABLE_CHANGE"]}</th>
            </tr>
          </thead>
          <tbody>
            {items.map((item) => (
              <tr className="manage-item" key={item.id}>
                <th>{item.name}</th>
                <th>{item.price}€</th>
                <th>
                  {item.available
                    ? texts[lang]["ITEM_AVAILABLE"]
                    : texts[lang]["ITEM_UNAVAILABLE"]}
                </th>
                <th>
                  <button
                    onClick={() => {
                      deleteItem(item.id);
                    }}
                  >
                    {texts[lang]["BUTTON_DELETE"]}
                  </button>
                </th>
                <th>
                  <button
                    onClick={() => {
                      changeAvailability(item.id, item.available);
                    }}
                  >
                    {item.available
                      ? texts[lang]["BUTTON_UNAVAILABLE"]
                      : texts[lang]["BUTTON_AVAILABLE"]}
                  </button>
                </th>
              </tr>
            ))}
          </tbody>
        </table>
      )}
      {items.length < 1 && <p>Ziadne polozky</p>}
    </div>
  );
};

export default AdminItemList;
